import { DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import { EcommerceOrder } from '@biz-nest/commerce-client';
import { FrontendLogger, IcommerceUser, Ireceipt, Iuser } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, MainBarTitleService,
  openClose,
  ViewOneCompose
} from '@front-shared-lib';
import { EagleInfoClientError, Icrumb, navByBus } from '@main-shared-lib';
import { AdminBackBarComponent } from '../../../misc/admin-back-bar/admin-back-bar.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';

@Component({
  selector: 'app-ecommerce-view-order',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    AdminBackBarComponent,
    AdminPageContentComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-ecommerce-order.component.html',
  styleUrl: './view-ecommerce-order.component.scss'
})
export class ViewEcommerceOrderComponent
implements OnInit, OnDestroy {
  viewOneCompose = new ViewOneCompose(
    EcommerceOrder,
    { route: ['/ecommerce-order/update'], eventName: 'updateEcommerceOrder' },
    this.router,
    this.route,
    this.eventbus
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  title = 'View';
  breadCrumbs: Icrumb[] = [
    {
      name: 'Dashboard',
      icon: '',
      link: '/'
    }, {
      name: 'EcommerceOrder',
      icon: '',
      link: '/ecommerce-order/list'
    }, {
      name: 'View EcommerceOrder',
      icon: 'visibility'
    }
  ];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  async ngOnInit() {
    try {
      await this.viewOneCompose.init();

      this.getBillingUser();

      this.title = this.viewOneCompose.oneItem?.urId || 'View';

      this.mainBarTitle.title = 'View EcommerceOrder - ' + this.title;

      this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
      this.metaTagService.updateTag({
        name: 'description',
        content: `Easily manage and edit order details to
          ensure accurate information and effective business operations.`
      });
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getBillingUser() {
    if (!this.viewOneCompose.oneItem) {
      throw new EagleInfoClientError('EcommerceOrder not found');
    }

    if (typeof this.viewOneCompose.oneItem.invoice.invoiceRelated?.billingUser === 'object') {
      return;
    };

    const id = typeof this.viewOneCompose.oneItem.invoice.invoiceRelated?.billingUser === 'string' ?
      this.viewOneCompose.oneItem.invoice.invoiceRelated?.billingUser : (this
        .viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser as Iuser)?._id;

    try {
      const one = await User.getOne(id);

      return one;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  getBillingUserInfo() {
    return this.viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser as IcommerceUser;
  }

  getBillingUserInfoNames() {
    if (!this.viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser ||
      typeof this.viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser === 'string') {
      return '';
    }

    return this.viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser?.firstName + ' ' + this
      .viewOneCompose.oneItem?.invoice.invoiceRelated.billingUser?.lastName;
  }


  viewReceipt(receipt: Ireceipt) {
    this.navigateByBus(
      ['/ecommerce-order/receipt/view/' + receipt?.urId],
      'oneItem',
      receipt
    );
  }


  ngOnDestroy() {
    this.viewOneCompose.cleanup?.();
  }
}

